@import "../assets/scss/mixin/responsive.scss";

.popular{
    margin-top: 20px;
    
    display: flex;
    flex-direction: column;
    
    align-items: center;
    width: 100%;
    max-width: 1200px;

    @include mq(mobile, max){
        max-width: 400px;
    }

    &__header{
        color: rgb(255, 255, 255);
        font-family: 'Open Sans', sans-serif;
        font-weight: 600;
    }
    &__aside{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: stretch;
        flex-wrap: wrap;

        width: 100%;
        max-width: 1200px;

        @include mq(desktop, max){
            max-width: 700px;
        }
        
        @include mq(mobile, max){
            justify-content: space-around;
            max-width: 400px;
        }
        
    }
    &__link{
        margin-top: 30px;
        display: flex;
        flex-direction: column;

        width: calc(100% / 5.5);

        text-decoration: none;
        transition: transform 0.1s;
        transform-style: preserve-3d;

        @include mq(desktop, max){
            width: calc(100% / 3.5);
        }

        @include mq(mobile, max){
            width: calc(100% / 2.5);
        }
        &:hover{
            transform: scale(1.1);
            @include mq(desktop, max){
                transform: scale(1);
            }
        }
    }
    &__details{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: stretch;
        
        width: 100%;
        min-height: 124px;
    }
    &__imdb{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        
        width: 100%;
    }
    &__title{
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;

        margin: 6px;
        padding: 0px;
        
        color: rgb(255, 255, 255);
        text-align: center;
        font-family: 'Open Sans', sans-serif;
        font-weight: 600;
        font-size: 16px;
    }
    &__genres{
        margin: 6px;
        padding: 0px;
        color: rgb(128, 128, 128);
        text-align: center;
        font-family: 'Open Sans', sans-serif;
        font-weight: 400;
        font-size: 14px;
        word-break: break-all;
        
        @include mq(mobile, max){
            font-size: 12px;
        }
    }
    &__imdbpoint{
        padding: 0 10px;
        
        color: rgb(255, 255, 255);
        font-family: 'Open Sans', sans-serif;
        font-weight: 600;
    }
    &__image{
        width: 100%;
        height: 295px;
        object-fit: cover;
        @include mq(mobile, max){
            height: 225px;
        }
    }
}
svg{
    padding: 0 10px;
    width: 36px;
    height: 24px;
}